import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { HeaderSessionComponent } from './header/headersession.component';
import { FooterSessionComponent } from './footer/footersession.component';
import { LayoutSessionComponent } from './layout/layoutsession.component';
import { ScrollToModule } from '@nicky-lenaers/ngx-scroll-to';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { ScrollSpyModule } from 'ngx-scrollspy';

@NgModule({
  declarations: [HeaderSessionComponent, FooterSessionComponent, LayoutSessionComponent],
  imports: [
    BrowserModule,
    CommonModule,
    RouterModule,
    NgbModule,
    ScrollSpyModule.forRoot(),
    ScrollToModule.forRoot()
  ]
})
export class LayoutSessionModule { }
