import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {AppRoutingModule} from './app-routing.module';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {AppComponent } from './app.component';
import { LayoutModule } from './layout/layout.module';

import {RegisterModule} from './modules/user/user.module';
import {StartupService} from './services/startup.service';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {LoginGuard} from './guards/login.guard';
import {InterceptorHttp} from './services/interceptorHttp.service';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';

//Start Material Design
import {MatButtonModule, MatCheckboxModule, MatInputModule} from '@angular/material';
import {MatCardModule} from '@angular/material/card';
import {MatIconModule} from '@angular/material/icon';
import {MatMenuModule} from '@angular/material/menu';
import {MatSidenavModule} from '@angular/material/sidenav';
import { FooterComponent } from './layout/footer/footer.component';
import { HeaderComponent } from './layout/header/header.component';
import { LayoutComponent } from './layout/layout/layout.component';

import { LayoutSessionModule } from './layoutsession/layoutsession.modele';
import { FooterSessionComponent } from './layoutsession/footer/footersession.component';
import { HeaderSessionComponent } from './layoutsession/header/headersession.component';
import { LayoutSessionComponent } from './layoutsession/layout/layoutsession.component';

//End Material Design

@NgModule({
  declarations: [
    AppComponent,
//    FooterComponent,
//    HeaderComponent,
//    LayoutComponent,
//	FooterSessionComponent,
//    HeaderSessionComponent,
//    LayoutSessionComponent
  ],
  imports: [
    BrowserModule,
    LayoutModule,
	LayoutSessionModule,
    AppRoutingModule,
    RegisterModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatCheckboxModule,
    MatInputModule,
    MatCardModule,
    MatIconModule,
    MatMenuModule,
    MatSidenavModule,
    ReactiveFormsModule,
    FormsModule,
  ],
  providers: [StartupService,LoginGuard,
    {
      provide: HTTP_INTERCEPTORS,
      multi: true,
      useClass: InterceptorHttp
    }],
  bootstrap: [AppComponent]
})
export class AppModule { }
