import {Observable} from 'rxjs';
import {RootObject} from '../../../../domain/models/register/rootobject.model';
import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {AppSettings} from '../../../../domain/appSettings';
import {CategoryRequest} from '../../../../domain/models/register/categoryrequest.model';

@Injectable()
export class CategoryService implements IRepository<CategoryRequest> {

  constructor(private http: HttpClient) {
  }

  public create(categoryRequest: CategoryRequest): Observable<RootObject> {
    var request= this.http.post<RootObject>(`http://default-bepartners-dev.apps.appcanvas.net/servicebroker/v1/catalog/categorias/consulta/`, categoryRequest);
      // .post<Register>(`${AppSettings.Core().EndPoints.API}/userthird/create`, register);
     return request
  }

}
