import {Observable} from 'rxjs';
import {RootObject} from '../../../../domain/models/register/rootobject.model';
import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {AppSettings} from '../../../../domain/appSettings';
import {Register} from '../../../../domain/models/register/register.model';

@Injectable()
export class RegisterService implements IRepository<Register> {

  constructor(private http: HttpClient) {
  }

  public create(register: Register): Observable<RootObject> {
    var request= this.http.post<RootObject>(`http://default-bepartners-dev.apps.appcanvas.net/servicebroker/v1/catalog/register/send/`, register);
      // .post<Register>(`${AppSettings.Core().EndPoints.API}/userthird/create`, register);
     return request
  }

}
